<template>
    <div class="add_list fl">
        <div class="add_class" @click="addClass">
            <div><i class="fa fa-plus-circle"></i> <span>{{titleName}}</span></div>
        </div>
        <ul>
            <li v-for="(item, index) in classifyList">
                <input type="radio" :id="item.catId" :value="item.catId" v-model="picked">
                <label :for="item.catId" @click="nextList(item, index)">{{ item.catName }}</label>
                <span class="revise">
                    <i @click="up(item, index)">↑</i>
                    <i @click="down(item, index)">↓</i>
                    <i class="el-icon-edit" @click="edit(item)"></i>
                    <i class="el-icon-delete" @click="remove(item, index)"></i>
                </span>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                picked: this.parameter
            }
        },
        props:['titleName', 'classifyList', 'name', 'parameter'],
        methods: {
            addClass() {
               this.$emit('addClass')
            },
            nextList(item, index) {
                this.$emit('nextList', item)
            },
            up(item, index) {
                if(index <= 0) {
                    this.$message('亲!已经到顶部了');
                    return
                }
                let i = index
                let t = item
                this.classifyList.splice(i, 1)
                let a = this.classifyList.splice(i-1, 0, t)
                this.picked = t.catId
            },
            down(item, index) {
                if(index >= this.classifyList.length-1) {
                    this.$message('亲!已经到底了');
                    return
                }
                let i = index
                let t = item
                this.classifyList.splice(i, 1)
                this.classifyList.splice(i+1, 0, t)
                this.picked = t.catId
            },
            edit(item) {
                this.$prompt('请输入值,最多10个字,开头结尾不能为空格', '编辑', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValue: item.catName,
                    inputValidator(value) {
                        let bl = /(^\s)|(\s$)/.test(value)
                        if(!value.length) {
                            return '不能为空'
                        }
                        else if(value.length > 10) {
                            return '不能超过10个字'
                        }
                        else if(bl) {
                            return '开头结尾不能为空格'
                        }
                    }
                }).then(({ value }) => {
                    item.catName = value
                }).catch(() => {
                });
            },
            remove(item, index) {
                this.$confirm('此操作将删除该分类, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    //如果是自己添加项可以直接删除，不请求接口， 是数据库里的删除要请求接口
                    if(item.auth) {
                        this.classifyList.splice(index, 1)
                        this.$emit('removeClass', { items: this.classifyList, item: item})
                        this.$message({
                            message: '删除成功',
                            duration: 1000,
                            type: 'success'
                        });
                    }
                    else {
                        this.$http({
                            url: '/productController/delClassify.do',
                            method: 'post',
                            params: {
                                catId: item.catId,
                            }
                        }).then( res => {
                            if(res.data.success) {
                                this.classifyList.splice(index, 1)
                                this.$emit('removeClass', { items: this.classifyList, item: item})
                                this.$message({
                                    message: '删除成功',
                                    duration: 1000,
                                    type: 'success'
                                });
                            }
                            else {
                                this.$message({
                                    message: res.data.msg,
                                    duration: 1000,
                                    type: 'info'
                                });
                            }
                        }).catch( error => {
                            this.$message({
                                message: '操作失败',
                                duration: 1000,
                                type: 'error'
                            });
                            console.log(error)
                        })
                    }

                }).catch(() => {});
            }
        },
        watch: {
            parameter(val) {
                this.picked = val
            }
        }
    }
</script>

<style lang="scss" scoped>
    .add_list {
        height: 100%;
        width: 23.375%;
        border-right: 1px solid #d6d7dc;
        overflow: hidden;
        .add_class {
            height: 60px;
            background: #333743;
            text-align: center;
            line-height: 60px;
            cursor: pointer;
            transition: background 1s;
            .fa-plus-circle {
                color: #fff;
                margin-right:7%;
                font-size: 15px;
            }
            span {
                color: #fff;
                font-size: 14px;
            }
        }
        .add_class:hover {
            background: #1F2D3D;
        }
        ul {
            height: 767px;
            overflow-y: auto;
            width: 110%;
            font-size: 14px;
            li {
                height: 52px;
                line-height: 52px;
                cursor: pointer;
                transition: background 1s;
                position: relative;
                label {
                    display: block;
                    height: 100%;
                    padding-left: 34px;
                    box-sizing: border-box;
                    transition: all .5s;
                    cursor: pointer;
                }
                input[type='radio'] {
                    position: absolute;
                    left: -500px;
                }
                input[type='radio']:checked+label {
                    background: #f4f5fb;
                }
                input[type='radio']:checked+label+.revise {
                    visibility: visible;
                    opacity: 1;
                }
                .revise {
                    position: absolute;
                    right: 10%;
                    top: 0;
                    opacity: 0;
                    transition: opacity .5s;
                    visibility: hidden;
                    i {
                        color: #38adff;
                        margin-right:5px;
                    }
                    i:nth-child(4) {
                        margin-right:0;
                    }
                }
            }
        }
    }
</style>



